const addClassName = function (el, className, fontName){
    let oldClass = el.getAttribute('class')
    const newClass = oldClass ? oldClass
                                    .replace(className, '')
                                    .split(' ')
                                    .filter(item => item) 
                               : []
    // 添加className到第一位
    newClass.unshift(className + '-' + fontName)
    el.setAttribute('class', newClass.join(' '))
}

module.exports = {
    checkObj,
    dynamicLoad,
    isPromise,
    addClassName 
}
