const { checkObj, addClassName } = require('../utils/index')
const Config = require('../config/index')
// const onUrlChange = require('../utils/checkUrl')
var gtransfer
var listener 
/**
 * 获取字体变化实例，单例模式
 * @param {*} oaCount 
 * @param {*} appKey 
 */
const getListener = function(oaCount, appKey, options){
    if(listener){
        return listener
    }
    if(window.createCopyListener){
        return window.createCopyListener(oaCount, appKey, options)
    }
    return null
}

/**
 * 获取更新函数，单例模式
 * @param {*} oaCount 
 * @param {*} appKey 
 */
const getGtransfer = function(oaCount, appKey, options){
    if(gtransfer){
        return gtransfer
    }
    // 防止调用失败，如果不存在gtransfer,则再生成一个
    const lis = getListener(oaCount, appKey, options)
    if(lis){
        lis.connect().then(function(transfer){
            gtransfer = transfer
        }).catch((msg) => {
            console.log('报错了')
            console.log(msg)
        })
        return listener
    }
}

/**
 * 数据更新后更新water
 * @param {*} el 
 * @param {*} binding 
 */
const updateWater = function (el, binding, vnode, oaCount, appKey, options){
    const text = binding.value
    const gtrans = getGtransfer(oaCount, appKey, options)
    if(gtrans && typeof gtrans == 'object'){
        gtrans.update()
        var unicodes = gtrans.transform(text)
        el.innerHTML = unicodes
    }
}

/**
 * 初始化指令
 * @param {*} oaCount 
 * @param {*} appKey 
 * @param {*} options 
 */
const initCopy = function (oaCount, appKey, options, fn){
    listener = getListener(oaCount, appKey, options)
    listener.connect().then(function(transfer){
        gtransfer = transfer
        fn && fn(gtransfer)
    }).catch((msg) => {
        console.log('报错了')
        console.log(msg)
    })
    return listener
}

/**
 * 初始化sdk
 */
let createCopy = (function(){
    return function (oaCount, appKey, options, Vue, router, flag){
        if (!oaCount || !appKey){
            console.log('请传入oaCount,appKey')
            return
        }
        if(!checkObj(options)){
            options = {}
        }
        if(flag){
            gtransfer = null
            listener = null 
        }
        let gtransferInit = initCopy(oaCount, appKey, options, function(){
            // onUrlChange(function(){
            //     const gtrans = getGtransfer(oaCount, appKey, options)
            //     if(gtrans && typeof gtrans == 'object'){
            //         gtrans.update()
            //     }
            // })
            // 切换路由需要更新实例
            if(router && typeof router == 'object'){
                typeof router.beforeEach == 'function' && router.beforeEach((to, from, next) => {
                    if(listener && listener.update && typeof listener.update == 'function'){
                        listener.update()
                        next()
                    }else{
                        next()
                    }
                })
            }
        })
        Vue.prototype.$copy = gtransferInit

        Vue.directive('maia-copy', {
            bind(el, binding){
                const text = binding.value
                gtransferInit.connect().then((gtr) => {
                    const unicodes = gtr.transform(text)
                    addClassName(el, Config.CLASS_NAME, options.fontName)
                    el.innerHTML = unicodes
                })
            },
            componentUpdated(el, binding, vnode, oaCount, appKey, options){
                updateWater(el, binding, vnode, oaCount, appKey, options)
            },
        })
    }
})()

module.exports = createCopy 
